export default async function nowRate() {
    try {
        const response = await fetch("https://api.manana.kr/exchange/rate.json");
        
        if (!response.ok) {
            throw new Error("    : " + response.status);
        }

        const data = await response.json();
        const rate = data[1].rate; //     

        return rate;
    } catch (error) {
        console.error(error);
        return 1; //     
    }
}

