import { useState } from 'react';
import './StateList.css'

export default function StateList() {
    const [satate, setsatate] = useState(['1', '2', '3']);
    //     

    return (
        <div className="statelistMain">
            <div>
            {satate.map((satate, i) => (
                <div key={i}>
                    <h3>Order Number : {satate[0]}</h3>
                    <p>  :</p>
                </div>
            ))
            }
            </div>
            <div className='stateDetail'>
                  

            </div>

        </div>
    );
}